import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col, message } from 'antd';
import { getStudyGradings, deleteStudyRanking } from 'services/studyRanking';

class RankLearning extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteStudyRanking(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'Xp hng',
        dataIndex: 'label',
        key: 'label',
        align: 'center'
      },
      {
        title: 'Tn ting anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
        align: 'center'
      },
      {
        title: 'T im',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
        align: 'center'
      },
      {
        title: 'n im',
        dataIndex: 'toGrade',
        key: 'toGrade',
        align: 'center'
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#rank-learning/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        )
      },
    ];
    return (
      <Card title="Danh mc xp hng hc lc">
        <CartHeadAction>
          <BtnAddNew href="#rank-learning/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getStudyGradings}
              columns={columns}
              // rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RankLearning />
  </Page>
);



